@use 'assets/scss/normalize';

body {
    font-family: 'Mulish', sans-serif;
}

.header {
    display: flex;
    align-items: center;
    padding: 20px 70px 20px 30px;
    margin-bottom: 30px;
}

.menu {
    &-button {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        background-color: #fff;
        width: 26px;
        height: 17px;
        margin-right: 25px;
    }
    &-line {
        width: 20px;
        height: 3px;
        background: #1f2022;
        border-radius: 1.5px;
    }
}

.logo {
    margin-right: 59px;
}

.input-group {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 830px;
    width: 50%;
    padding-right: 17px;
    background: #ebebeb;
    border-radius: 22px;
}

.input-search {
    padding: 12px 23px;
    background-color: transparent;
    border: none;
}

.button-search {
    width: 19px;
    height: 19px;

    background-color: transparent;
}

.icons {
    display: flex;
    align-items: center;
    margin-left: auto;
}

.icon-search {
    fill: #000;
}

.icons-link {
    display: inline;
    opacity: 0.24;
}

.icons-link:hover {
    opacity: 0.7;
}

.icons-link:not(:last-child) {
    margin-right: 50px;
}

.user {
    margin-left: 45px;

    &-avatar {
        width: 40px;
        height: 40px;
        border-radius: 50%;
    }
}

.sidebar {
    padding-left: 30px;
    color: #898989;

    & .icon {
        width: 26px;
        height: 20px;
        fill: #898989;
    }
}
.nav {
    &-menu {
        margin-bottom: 58px;
    }

    &-link {
        display: flex;
        align-items: center;
        margin-bottom: 28px;

        &:hover {
            color: #ff0000;

            & .icon {
                fill: #ff0000;
            }
        }
    }

    .nav-link:nth-child(3) {
        margin-bottom: 60px;
    }

    &-text {
        display: inline-block;
        margin-left: 10px;
    }
}

.subscriptions {
    margin-bottom: 104px;

    &-title {
        color: #000000;
        font-size: 1.8rem;
        font-weight: bold;
        margin-left: 3px;
        margin-bottom: 30px;
    }
}
.subscription {
    &-link {
        display: flex;
        align-items: center;
        margin-bottom: 23px;

        &:hover {
            color: #ff0000;
        }
    }
    &-img {
        display: inline-block;
        width: 26px;
        height: 26px;
        border-radius: 50%;
    }
    &-text {
        @extend .nav-text;
    }
}

.setting-link {
    margin-bottom: 40px;
}
